<template>
	<div
		class="tags-wrapper"
		:class="align === 'space-between' ? 'tags-wrapper_align_space-between' : ''"
		ref="tagsWrapper"
	>
		<template v-if="tags.length">
			<div
				class="tag"
				v-for="(tag, index) in tags"
				:key="index"
				ref="tags"
			>
				<v-chip
					class="tag__element"
					color="transparent"
				>
					<v-icon
						v-if="tag.icon"
					>
						{{ tag.icon }}
					</v-icon>

					{{ tag.text }}
				</v-chip>

				<v-icon
					v-if="!(tags.length - 1 === index)"
				>
					mdi-circle-small
				</v-icon>
			</div>
		</template>
	</div>
</template>

<script>
	export default {
		components: 'ListTags',
		props: {
			tags: {
				type: Array,
				required: true,
			},
			align: {
				type: String,
			},
		},
		methods: {
			toggleTags() {
				const wrapper = this.$refs.tagsWrapper;
				const tag = this.$refs.tags[0];
				const tagHeight = tag.offsetHeight;

				wrapper.style.setProperty(
					'--tag-height',
					`${tagHeight}px`
				);
			}
		},
		mounted() {
			this.toggleTags();
		},
		created() {
			window.addEventListener('resize', this.toggleTags);
		}
	}
</script>

<style lang="scss">
	.tags-wrapper {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		gap: 5px;
		height: var(--tag-height);

		overflow: hidden;

		&_align_space-between {
			justify-content: space-between;
		}
	}

	.tag {
		display: inline-flex;
		align-items: center;
		gap: 5px;
		flex-shrink: 0;

		&__element {
			--tag-element-space: 0;

			padding-right: var(--tag-element-space);
			padding-left: var(--tag-element-space);

			&:hover:before {
				opacity: 0 !important;
			}

			.v-chip__content {
				gap: 5px;

				white-space: nowrap;
			}
		}
	}
</style>
